import React, { Component } from "react";
import firebase from "../firebase";
class Home extends Component {
  constructor(props) {
    super(props);
    this.ref = firebase.firestore().collection("Jobs");
    this.unsubscribe = null;
    this.userId =localStorage.getItem("userid");
    this.state = {
      boards: []
    };
  }

  onCollectionUpdate = querySnapshot => {
    const boards = [];
    querySnapshot.forEach(doc => {
      const {
        title,
        jobType,
        numberJob,
        otherSpecification,
        postId
      } = doc.data();

      if (postId === this.userId) {
        boards.push({
          key: doc.id,
          doc,
          title,
          jobType,
          numberJob,
          otherSpecification,
          postId
        });
      }
    });
    this.setState({
      boards
    });
  };

  componentDidMount() {
    this.unsubscribe = this.ref.onSnapshot(this.onCollectionUpdate);
  }

  render() {
    return (
      <div>
        {this.state.boards.length !== 0 ? (
          <div className="container-fluid">
            <div className="row">
              {this.state.boards.map(board => (
                <div className="col-12 mt-3">
                  <div className="card">
                    <div className="card-horizontal">
                      <div className="img-square-wrapper">
                        <img
                          className=""
                          src="https://encrypted-tbn0.gstatic.com/images?q=tbn:ANd9GcTVPh-yFTXXF7kS-UHCGMkmoa7CJp9mBqLPiPSQIszewER8lQs4"
                          alt="Card image cap"
                        />
                      </div>
                      <div className="card-body">
                        <h4 className="card-title">{board.title}</h4>
                        <h4 className="card-title">{board.jobType}</h4>
                        <h4 className="card-title">{board.numberJob}</h4>
                        <p className="card-text">{board.otherSpecification}</p>
                      </div>
                    </div>
                    <div className="card-footer">
                      <button className="btn btn-success">View Details</button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        ) : (
          <h1>Create Job Now</h1>
        )}
      </div>
    );
  }
}
export default Home;
