import React from "react";
import "./CompanyComponent/style.css";
import SideBar from "./CompanyComponent/sidebar";
import AddJob from "./CompanyComponent/AddJob";
import Home from "./CompanyComponent/Home";
import Profile from "./CompanyComponent/Profile";
import Logout from "./CompanyComponent/Logout";

import { BrowserRouter as Router, Route } from "react-router-dom";
import About from "../component/example";
const Company = () => (
  <div id="App">
    <SideBar pageWrapId={"page-wrap"} outerContainerId={"App"} />

    <div id="page-wrap">
      <Router>
        <div>
          <Route exact path="/" component={Home} />
          <Route exact path="/Addjob" component={AddJob} />
          <Route exact path="/profile" component={Profile} />
          <Route exact path="/viewUser/:id" component={About} />
          <Route exact path="/EditJob/:id" component={About} />
          <Route exact path="/logout" component={Logout} />
        </div>
      </Router>
    </div>
  </div>
);
export default Company;
