import React from "react";
import Apply from "./UserComponent/Apply";
import Profile from "./UserComponent/Profile";
import Home from "./UserComponent/Home";
import Logout from "./UserComponent/Logout";

import "./UserComponent/style.css";
import { BrowserRouter as Router, Route } from "react-router-dom";
import About from "../component/example";
import SideBar from "./UserComponent/sidebar";
const User = () => (
  <div>
    <div id="App">
      <SideBar pageWrapId={"page-wrap"} outerContainerId={"App"} />

      <div id="page-wrap">
        <Router>
          <div>
            <Route exact path="/" component={Home} />
            <Route exact path="/application" component={Apply} />
            <Route exact path="/profile" component={Profile} />
            <Route exact path="/jobs/:id" component={About} />
            <Route exact path="/logout" component={Logout} />
          </div>
        </Router>
      </div>
    </div>
  </div>
);
export default User;
