import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter as Router, Route, Link } from "react-router-dom";
import Company from "./component/Comapny.js";
import User from "./component/User.js";
import "./styles.css";
import firebase from "./component/firebase";
class App extends React.Component {
  state = {
    value:""
  };

  componentDidMount = () => {
    
    const userId=localStorage.getItem("userid")
    console.log(userId,"dsadasd")
    const ref = firebase
      .firestore()
      .collection("User")
      .doc(userId);
    ref.get().then(doc => {
      if (doc.exists) {
        const board = doc.data();
        if (board.type === "Company") {
          console.log("hiii")
          this.setState({
            value: true
          });
        } else if(board.type === "User") {
          this.setState({
            value: false
          });
        }
        else
        {
          console.log("no location")
        }
      } else {
        console.log("No such document!");
        // window.location.replace("../index.html");

      }
    });
    //firesbase auth call with Userr databse if type of suer is Comapy then value is true other wise false
  };
  render() {
    return (
      <Router>
        <div>{this.state.value ? <Company /> : <User />}</div>
      </Router>
    );
  }
}
const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
